import uniIcons from "../../components/uni-icons/uni-icons.vue"
import HMsearch from '../../components/HMsearch.vue'
// import Attachment from '../../components/jin-attachment/jin-attachment.vue';
var res = global.isLogin();
var openid = uni.getStorageSync('openid');
console.log(openid)

export default {
	components: {
		uniIcons,
		HMsearch
		// Attachment
	},
	data() {
		return {
			title:"",
			content:"",
			list: [],
			isShow: true,
			img_url: `${this.img_url}`,
			temp_img: [],
			temp_video:[],
			current_shenfen: 0,
			shenfen_arr: [{
				value: 0,
				name: '匿名',
				checked: 'true'
			}, {
				value: 1,
				name: '公开'
			}]
	
		}
	},
	methods: {
		radioChange(evt) {
			console.log(evt.detail.value);
		
			this.current_shenfen = evt.detail.value;
		
			for (let i = 0; i < this.shenfen_arr.length; i++) {
				this.shenfen_arr[i].checked = 'false';
				if (this.shenfen_arr[i].value === evt.target.value) {
					this.current_shenfen = i;
					this.shenfen_arr[i].checked = 'true';
					break;
				}
			}
		
		
		},
				
				
		goHotSearch(){
			uni.navigateTo({
				url: '/pages/hotSearch/hotSearch'
			});
		},
		/**
		 * 问答list
		 * @wentiType类型
		 */
		getLiuyanList() {	
			this.req.request('/list/'+openid,{
				
				"zmyZs":0
			},'POST',(res) =>{
				if(res.success){
				console.log(res)
				this.list = res.message	
				}else{
					uni.showModal({
						title: '提示',
						content: "请先登录吧！",
						showCancel: false,
						success: function() {
							uni.navigateTo({
								url: ""
							});
						}
					})
				}
				
		
			},function(e) {
				uni.showToast({
					title: e.errMsg,
					mask: true,
					duration: 1200,
					icon: 'none'
				});
			});
			
			
		},
		searchResult(){
			this.getLiuyanList()
		},
		gotoDetail(i){
		// console.log(i)
		let url = "/pages/question/question?id="+i;
			uni.navigateTo({
				url: url
			})
		},
		// 上传图片
		choose_img() {
			let _this = this,
				tempFilePaths;
			uni.chooseImage({
				count: 6,
				success: (res) => {
					tempFilePaths = res.tempFilePaths[0];
					uni.uploadFile({
						url: this.img_url+'/system/phone/upload/'+openid,
						filePath: tempFilePaths,
						name: 'file',
						success: (e) => {	
							let img_list = JSON.parse(e.data);
							console.log(img_list)
							console.log(e)
							// var imgType="gif"; "jpeg"; "jpg"; "bmp"; "png";
							// var imgType=["gif","jpeg","jpg","bmp","png"];
							// var videoType=["avi","wmv","mkv","mp4","mov","rm","3gp","flv","mpg","rmvb"];
							var type = img_list.data
							type.map(fileName =>{
								var first = fileName.lastIndexOf(".");//取到文件名开始到最后一个点的长度
								var namelength = fileName.length;//取到文件名长度
								var filesuffix = fileName.substring(first + 1, namelength );//截取获得后缀名
								if(filesuffix =='jpg'||'png'){
									_this.temp_img.push(img_list.data);
								}else if(filesuffix =='mp4'){
									_this.temp_video.push(img_list.data);
								}
							
							})
						
							
							console.log(_this.temp_img);
						},
						fail: (e) => {
							uni.showModal({
								title: '提示',
								content: e.message,
								success: (e) => {}
							})
						}
					});
		
				}
			});
		},
		del_img(e) {
						this.temp_img.splice(e, 1);
					},
		
	
		// 提交问题
		submit(){
			
			if(!this.title||!this.content){
				uni.showToast({
					title: '请输入完整的内容~',
					mask: true,
					duration: 1100,
					icon: 'none'
				});
				return
			}else if(!res){
				uni.showModal({
					title: '提示',
					content: "请先登录吧！",
					showCancel: false,
					success: function() {
						uni.navigateTo({
							url: ""
						})	
					}
				})
			}
			
			this.req.request('/addwt/'+openid, {
				wentiTitle:this.title,
				wentiNeirong:this.content,
				sfnmStatus:this.current_shenfen,
				images:this.temp_img,
				videos:this.temp_video,
				"wentiType": "1"
			}, 'POST', (res)=> {
				console.log(res)
				if (res.success) {
					this.title="",
					this.content="",
					this.temp_img=[]
					uni.showToast({
						title: res.message,
						mask: true,
						duration: 1200,
						icon: 'none'
					});
					setTimeout(()=>{
						uni.navigateTo({
						url: '/pages/my/my'
					})
					},2000)	
				} 
			
			},function(e) {
				uni.showToast({
					title: e.message,
					mask: true,
					duration: 1200,
					icon: 'none'
				});
			})
			
		},
		 
	},
	
	// onLoad相当于created生命周期
	onLoad() {
		this.getLiuyanList()
	}

}
